import Brush from "../module/Brush";
let _canvas;
export default class SketchBook {
    constructor(canvas, width, height, layer = 1) {
        _canvas = canvas;
        console.log(_canvas)

        this._init();
    }

    _init() {
        // this.defaultTool = new Brush(this.canvas);
        this.canvas = new fabric.Canvas(_canvas, {
            isDrawingMode: true
        });

    }


}


